import React from 'react';
import logo from './logo.svg';
import './App.css';
import GoogleLogin from "@leecheuk/react-google-login";

const CLIENT_ID = '35295486372-norf8611p9k0pfgev5u2oi3fkkcs3vb5.apps.googleusercontent.com'; // Replace with your actual client ID

function App() {
  const onSuccess = (response: unknown) => {
    console.log('Login Success: currentUser:', response);
    // Here you could also perform actions on the token or use it to make API calls
  };

  const onFailure = (response: Error) => {
    console.log('Login Failed: res:', response);
  };

  return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.tsx</code> and save to reload.
          </p>
          <GoogleLogin
              clientId={CLIENT_ID}
              buttonText="Login with Google"
              onSuccess={onSuccess}
              onFailure={onFailure}
              cookiePolicy={'single_host_origin'}
              scope="https://www.googleapis.com/auth/calendar.readonly"
          />
          <a
              className="App-link"
              href="https://reactjs.org"
              target="_blank"
              rel="noopener noreferrer"
          >
            Learn React
          </a>
        </header>
      </div>
  );
}

export default App;
